<template>
  <div>
    <v-row align="center" class="wrap" no-gutters>
      <v-col md="3" sm="12" lg="3">
        <v-text-field
          v-model="busTitulo"
          label="Título"
          outlined
          dense
          autocomplete="off"
          :cache-items="false"
          prepend-icon="list_alt"
          >
        </v-text-field>
      </v-col>
      <v-col md="3" sm="12" lg="3">
        <v-text-field
            v-model="busDescripcion"
            outlined
            dense
            label="Descripción"
            autocomplete="off"
            :cache-items="false"
            prepend-icon="list_alt"
          >
        </v-text-field>
      </v-col>
      <v-col class="text-center" md="3" sm="12" lg="3">
        <v-text-field
            v-model="busReferencia"
            label="Referencia"
            outlined
            dense
            autocomplete="off"
            :cache-items="false"
            prepend-icon="list_alt"
          >
        </v-text-field>
      </v-col>
      <v-col class="text-center" md="3" sm="12" lg="3" v-if="visEstado=='1'">
        <v-autocomplete
            v-model="busEstadoSel"
            :items = "estadospropuestas"
            outlined
            dense
            label="Estado"
            autocomplete="off"
            :cache-items="false"
            prepend-icon="list_alt"
            clearable
        >
        </v-autocomplete>
      </v-col>
      <!--<v-col class="text-center" md="4" sm="12" lg="4" v-else>
        <span></span>
      </v-col>-->
      <v-col md="3" sm="12" lg="3">
        <v-text-field
            v-model="busFecPubIni"
            label="Publicado Entre"
            outlined
            dense
            type="date"
            autocomplete="off"
            :cache-items="false"
            prepend-icon="event"
            clearable
        >
        </v-text-field>
      </v-col>
      <v-col md="3" sm="12" lg="3">
        <v-text-field
            v-model="busFecPubFin"
            outlined
            dense
            type="date"
            label="Y"
            autocomplete="off"
            :cache-items="false"
            readonly
            prepend-icon="event"
        >
        </v-text-field>
      </v-col>
      <v-col md="3" sm="12" lg="3">
        <v-text-field
            v-model="busFecLimIni"
            label="Fecha Límite Entre"
            outlined
            dense
            type="date"
            autocomplete="off"
            :cache-items="false"
            readonly
            prepend-icon="event"
        >
        </v-text-field>
      </v-col>
      <v-col md="3" sm="12" lg="3">
        <v-text-field
            v-model="busFecLimFin"
            outlined
            dense
            type="date"
            label="Y"
            autocomplete="off"
            :cache-items="false"
            prepend-icon="event"
            clearable
        >
        </v-text-field>
      </v-col>
      <v-col md="3" sm="12" lg="3">
        <v-autocomplete
            v-model="busTipoContratacion"
            :items="tiposcontratacion"
            item-text="text"
            item-value="value"
            label="Tipo Contratación"
            outlined
            dense
            autocomplete="off"
            :cache-items="false"
            prepend-icon="list_alt"
            clearable
        >
        </v-autocomplete>
      </v-col>
    </v-row>
    <v-row justify="end">
      <v-btn
        color="primary"
        text
        @click="save"
        >
        Buscar
      </v-btn>
    </v-row>
  </div>
</template>
<script>
import { anunciosProcAdquisicion } from '@/services/api'
export default {
  props: ['busEstado', 'visEstado'],
  data () {
    return {
      busTitulo: '',
      busDescripcion: '',
      busReferencia: '',
      busFecPubIni: '',
      busFecPubFin: '',
      busFecLimIni: '',
      busFecLimFin: '',
      busTipoContratacion: '',
      busEstadoSel: '',
      tiposcontratacion: [],
      estadospropuestas: []
    }
  },
  mounted () {
    this.setFechaActual()
    this.getEstadospropuestas()
    this.getTiposcontratacion()
  },
  methods: {
    async getEstadospropuestas () {
      try {
        const { data } = await anunciosProcAdquisicion.getEstadoPropuesta
        this.estadospropuestas = data
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    save () {
      this.$emit('search', {
        busTitulo: this.busTitulo,
        busDescripcion: this.busDescripcion,
        busReferencia: this.busReferencia,
        busFecPubIni: this.busFecPubIni,
        busFecPubFin: this.busFecPubFin,
        busFecLimIni: this.busFecLimIni,
        busFecLimFin: this.busFecLimFin,
        busTipoContratacion: this.busTipoContratacion,
        busEstado: this.busEstado !== '' ? this.busEstado : this.busEstadoSel
      })
    },
    setFechaActual () {
      const fechaActual = new Date()
      const dia = fechaActual.getDate()
      const mes = fechaActual.getMonth() + 1
      const anno = fechaActual.getFullYear()
      const fechatxt = anno.toString() + '-' + mes.toString().padStart(2, '00') + '-' + dia.toString().padStart(2, '00')
      this.busFecPubFin = fechatxt
      this.busFecLimIni = fechatxt
    },
    async getTiposcontratacion () {
      try {
        const { data } = await anunciosProcAdquisicion.getTipoContratacion
        const tipocontrata = data
        for (let index = 0; index < tipocontrata.length; index++) {
          const element = tipocontrata[index]
          switch (element) {
            case 'dir_personal':
              this.tiposcontratacion.push({ text: 'CONVOCATORIA DE PERSONA NATURAL', value: element })
              break
            case 'dir_bien':
              this.tiposcontratacion.push({ text: 'PRESTACIÓN DE SERVICIOS / CONSULTORÍAS INDIVIDUALES', value: element })
              break
            case 'dir_juridica':
              this.tiposcontratacion.push({ text: 'CONVOCATORIA DE PERSONA JURÍDICA', value: element })
              break
            case 'lista_corta':
              this.tiposcontratacion.push({ text: 'LISTA CORTA', value: element })
              break
            case 'conv_publica':
              this.tiposcontratacion.push({ text: 'CONVOCATORÍA PÚBLICA', value: element })
              break
            default:
              break
          }
        }
        // this.tiposcontratacion = data
      } catch (error) {
        console.log(error)
      } finally {
      }
    }
  }
}
</script>
